import React from 'react';

const NewsList =(props) => {
    console.log(">>>>>",props)

    const renderList = ({newsdata}) => {
        if(newsdata){
            return newsdata.map((data) => {
                return(
                    <div>
                        <h3>{data.title}</h3>
                        <p>{data.feed}</p>
                    </div>
                )
            })
        }
        
    }

    return(
        <div>
          {renderList(props)}
        </div>
    )
}

export default NewsList;